//
// Progress bars
// --------------------------------------------------

// Bar itself
// -------------------------

// Outer container
.progress {
    border-radius: inherit;
    .box-shadow(inherit);
}

// Bar of progress
.progress-bar {
    .box-shadow(inherit);
    .transition(inherit);
}



